import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '佣金明细' //导航栏文字
			},
			params: {
				page: 1, //页数
				size: 10, //每页几条
			}, //分页参数
			withdrawData: {}, //提现数据
			withdrawList: [] // 提现列表
		}
	},
	onShow() {
		this.initData()
	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.withdrawList.length >= this.withdrawData.total) {
			return;
		}
		this.params.page++;
		this.getWithdraw()
	},
	methods: {
		initData() {
			//重置分页参数
			this.withdrawData = {}
			this.withdrawList = []
			this.params.page = 1
			this.getWithdraw();
		},
		getWithdraw() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.cashOutRecord, this.params).then((res) => {
				that.withdrawData = res.data;
				if(that.params.page == 1) {
					that.withdrawList = res.data.data
				} else {
					that.withdrawList = that.withdrawList.concat(res.data.data)
				}
			});
		}
	}
}
